import React from 'react';

import './ImageViewer.css';

import ImageLabel from '../Labels/ImageLabel/ImageLabel.js';

const ImageViewer = (props) => {
    var { className, hideImage, ...rest } = props;
    if (className == undefined) {
        className = "ImageViewer";
    }
    else {
        className += " ImageViewer";
    }
    if (hideImage) {
        var style = {"display": "none"};
    }
    else {
        var style = {};
    }
    return (
        <ImageLabel className={className} style={style} {...rest}/>
    );
}

export default ImageViewer;