import React from "react";

import "./Switch.css";

const Switch = (props) => {
  var { className, isChecked, handleToggle, ...rest } = props;
  if (className == undefined) {
    className = "Switch";
  }
  else {
    className += " Switch";
  }
  if (isChecked) {
    var labelClassName = "switch-label label-switched";
    var ballClassName = "switch-ball ball-switched";
  }
  else {
    var labelClassName = "switch-label";
    var ballClassName = "switch-ball";
  }
  return (
    <div className={className} {...rest}>
      <input type="checkbox" className={"switch-checkbox"} checked={isChecked} onChange={(event) => handleToggle(event.target.checked)} id={"switch-input"}/>
      <label className={labelClassName} htmlFor={"switch-input"}>
        <div className={ballClassName}></div>
      </label>
    </div>
  );
};

export default Switch;